body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-italianno: Italianno;
  --font-coda: Coda;

  /* font sizes */
  --font-size-81xl: 100px;
  --font-size-31xl: 50px;
  --font-size-51xl: 70px;
  --font-size-xl: 20px;
  --font-size-13xl: 32px;
  --font-size-41xl: 60px;
  --font-size-4xl: 23px;
  --font-size-24xl: 43px;
  --font-size-45xl: 64px;
  --font-size-11xl: 30px;
  --font-size-3xs: 10px;
  --font-size-mini: 15px;
  --font-size-6xl: 25px;
  --font-size-71xl: 90px;
  --font-size-21xl: 40px;

  /* Colors */
  --color-white: #fff;
  --color-black: #000;
  --color-red: #ff0000;
  --color-gray: rgba(0, 0, 0, 0.5);

  /* border radiuses */
  --br-mini: 15px;
  --br-81xl: 100px;
}
